import { createApp } from 'vue'
import App from './App.vue'
import router from './router';
import HomeContainer from '@/views/components/HomeContainer.vue';

import { IonContent, IonicVue, IonPage,IonIcon,IonLabel, IonCard, IonCardContent, IonButton, IonButtons, IonHeader, IonImg, IonInput, IonTitle, IonTextarea,IonSelect,IonSelectOption, IonCheckbox, IonBackButton } from '@ionic/vue';

/* Core CSS required for Ionic components to work properly */
import '@ionic/vue/css/core.css';

/* Basic CSS for apps built with Ionic */
import '@ionic/vue/css/normalize.css';
import '@ionic/vue/css/structure.css';
import '@ionic/vue/css/typography.css';

/* Optional CSS utils that can be commented out */
import '@ionic/vue/css/padding.css';
import '@ionic/vue/css/float-elements.css';
import '@ionic/vue/css/text-alignment.css';
import '@ionic/vue/css/text-transformation.css';
import '@ionic/vue/css/flex-utils.css';
import '@ionic/vue/css/display.css';

/* Theme variables */
import './theme/variables.css';

const app = createApp(App)
  .use(IonicVue)
  .use(router);
app.component('ion-content', IonContent);
app.component('ion-page', IonPage);
app.component('ion-icon', IonIcon);
app.component('ion-label', IonLabel);
app.component('ion-card', IonCard);
app.component('ion-img', IonImg);
app.component('ion-header', IonHeader);
app.component('ion-card-content', IonCardContent);
app.component('home-container', HomeContainer);
app.component('ion-button', IonButton);
app.component('ion-buttons', IonButtons);
app.component('ion-back-button', IonBackButton);
app.component('ion-input', IonInput);
app.component('ion-title', IonTitle);
app.component('ion-textarea', IonTextarea);
app.component('ion-select', IonSelect);
app.component('ion-select-option', IonSelectOption);
app.component('ion-checkbox', IonCheckbox);
router.isReady().then(() => {
  app.mount('#app');
});